@media (min-width: 1400px) {
  .catalog-wrapper {
    width: 1440px;
    margin: 74px auto 0 auto;
  }
}

.catalog-wrapper__heading {
  margin: 0;
  margin-bottom: 50px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.card-product__heading {
  margin: 0;
  margin-bottom: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #222222;
}
@media (min-width: 768px) {
  .card-product__heading {
    text-align: center;
  }
}

.show-more__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .show-more__heading {
    text-align: center;
  }
}

.show-more__text {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .show-more__text {
    text-align: center;
  }
}

.form-wrapper {
  margin: 0 20px;
}
@media (min-width: 768px) {
  .form-wrapper {
    margin: 0 30px;
  }
}
@media (min-width: 1400px) {
  .form-wrapper {
    width: 1440px;
    margin: 0 auto;
  }
}

.form-wrapper__heading {
  margin: 0;
  margin-bottom: 37px;
  padding-top: 27px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
  }
}

.site-list__item {
  padding: 0 auto;
  padding-top: 24px;
  padding-bottom: 22px;
}
@media (min-width: 768px) {
  .site-list__item {
    margin-top: 8px;
  }
  .site-list__item:not(:last-child) {
    margin-right: 25px;
  }
}

.site-list__link {
  font-family: "Oswald", "Arial", sans-serif;
  color: black;
}

.card-desc {
  margin-bottom: 13px;
}
@media (min-width: 768px) {
  .card-desc {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.card-desc__text {
  margin: 0;
  margin-bottom: 6px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}
@media (min-width: 768px) {
  .card-desc__text {
    text-align: center;
  }
}

@media (min-width: 768px) {
  .extra-product-item {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1400px) {
  .extra-product-item {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.extra-product-item__heading {
  margin: 0;
  margin-bottom: 13px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}

.extra-product-item-desc {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.extra-product-item-desc__text {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}